body{
    width: 100vw!important;
    margin: auto;
    padding: 0;
}
.container-arp{
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("/assets/backgrounds/img3.jpg");
    background-size: cover;
    background-attachment: fixed;
    width: 100%!important;
    margin: auto;
    padding: 0;
    
}
.row-arp{    
    width: 100%;
}

  container{
    width: 100%!important;
  }

  .img-arp{
    width: 50%;
    margin: auto;
    margin-top: 10vw;
    margin-bottom: 5vw;
  }

  .header-arp{
    width: 100%;
    text-align: center;
    position: relative;
    color: white;
  }

  .header-buttons{
    width: 100%;
    text-align: center;
  }

  .header-buttons img{
    width: 2vw;
    margin-left: 45px;
    margin-right: 45px;
    margin-bottom: 45px;
    rotate: 0deg;    
    transition:  0.3s ease;
    border-radius: 100%;
  }

  .header-buttons img:hover{
    rotate: 0deg;
    box-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px;
    transition:  0.2s ease;
  }